
@import url(font.css);


:root{
    --text-gray:#3f4954;
    --text-light:#686666da;
    --midnight:#104f55;
    --bg-color:#0f0f0f;
    --white:#ffffff;
    --sky: linear-gradient(to top, #c2cbfb 0%, #a6c1ee 100%);
    --lexend:'Lexend',cursive;
    --Anton:'Anton',cursive;
    --Abel:'Abel',cursive;
}


body{
   background-color: #000000;
   background-image: linear-gradient(147deg, #000000 0%, #434343 74%);

}
section{
    margin-top: 3rem;
    color: var(--white);
    /* margin-left: 2rem; */
}
ul{
    list-style-type:none ;
}
h1{
    font-family: var(--lexend);
    font-size: 2.5rem;

}
h2{
    font-family: var(--lexend);
}
h3{
    font-family: var(--lexend);
    font-size: 1.3rem;
}
.pic{
    display: flex;
    margin: auto;
    /* border: 2px solid red; */
    margin-right: 6rem;

}

.Blobpicture{
    transform:rotateZ(-85deg) ;
    position: absolute;
    margin-top: 15rem;
    /* margin-left: 1rem; */
}
.Blobpicture img{
    width: 17rem;
    height: 26rem;

}
hr{
    height: 7px;
    border: none;
    border-radius: 2rem;
    margin-left: 40rem;
    margin-right: 6rem;
    /* background-color:#6495ed / rgb(100,149,237); */
    background-image: linear-gradient(to right, #1ad4e8, #07b0d2, #1458b0);
    /* color: ; */
}
.picture img{
    position: relative;
    width: 27rem;
    height: 30rem;
}
.tags{
    margin-top: 2rem;
}
 .tags i{
    margin-right: 1rem;
    font-size: 2rem;
    display: inline-block;
    padding: 8px 16px;
    background: linear-gradient(to right,  #1ad4e8, #07b0d2);
    border-radius: 4px;
    color: #104f55;
  /* margin: 4px; */
}
.tags:hover i{
    cursor: pointer;
}

.home p{
    width: 40rem;
    font-size: 1.3rem;
    text-align: justify;
    word-spacing: 2px;
}
.profile{
    display: flex;
    margin: 1rem 3rem;
}
.name-about h1{
    font-weight: 600;
    display: flex;
    margin: 1rem 0rem;
}
.name-about h3{
    margin-top: 4rem;
    font-size: 1.5rem;

}
.name-about h1 .name{
   color: #01dbf8e4;
}

@media (max-width:1080px){
    hr{
        display: none;
    }
    .profile{
        display: flex;
        flex-wrap: wrap;
        flex-direction: column-reverse;
    }

    .picture img{
        width: 30rem;
        height: 35rem;
        margin-right: auto;
    }
    /* .Blobpicture img{
        width: 7rem;
        height: 10rem;
    
    } */
}

@media (max-width:600px) {
    hr{
        display: none;
    }
    .home p{
        width: 21rem;
        text-align: justify;
    }
    .picture img {
        height: 18rem;
        position: relative;
        width: 15rem;
	}
    
}
@media (max-width:480px){
    section{
        margin-left: 0rem;
    }
    .home p{
        padding-right: 10px;
        font-size: 1.1rem;
        width: 82%;
    }
    .picture img {
        height: 22rem !important;
        width: 19rem !important;
    }

}

@media (max-width:360px){

    .home p{
        padding-right: 10px;
        font-size: 1.1rem;
        width: 17rem !important;
    }


}
