
@import url(font.css);


:root{
    --text-gray:#3f4954;
    --text-light:#686666da;
    --midnight:#104f55;
    --bg-color:#0f0f0f;
    --white:#ffffff;
    --sky: linear-gradient(to top, #c2cbfb 0%, #a6c1ee 100%);
    --lexend:'Lexend',cursive;
    --Anton:'Anton',cursive;
    --Abel:'Abel',cursive;
}
*{
  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-weight: 500;
}
a{
  text-decoration: none;
}
.CertHead{
  text-align: center;
}
.Certh3{
  font-size: 2rem;
}
.Certh4{
  font-size: 1rem;
  color: #e2dada;

}
.certs {
    display: flex;
    flex-wrap: wrap;
    padding: 10px; 
    justify-content: center;   
  }

  .cert {
    width: 300px;
    overflow: hidden;
    margin: 1rem;
    border-radius: 1rem;
    background: transparent !important;
    box-shadow: 0 2px 4px rgba(17, 164, 255, 0.988);
    animation: ease-in;
  
  }
  .cert:hover{
    background-color: #333;
    color: var(white);
    /* cursor: pointer; */
    transform: scale(1.03);
    transition: all 1s ease;
  }
  
  .cert:last-child {
    margin-right: 0;
  }
  
  .cert-title {
    font-size: 18px;
    font-weight: bold;
    margin: 16px;

  }

  
  .cert p {
    margin: 0 16px 16px;

  }
  
  .cert .certurl {
    color: #333;
    /* text-decoration: none; */
    font-weight: bold;

  }

  .frontend,.backend,.other{
    width: 400px;
  }
  
  .cert a{
    background: #5dabff;
    color: white;
    padding: 15px 20px;
    display: block;
    text-align: center;
    margin: 20px 15px;

  }


  .skill-button {
    display: inline-block;
    flex-wrap: wrap;
    margin: 5px;
    height: auto;
    padding: 5px 5px;
    width: 46%;
    overflow: hidden;
    justify-content: center;
    font-size: 14px;
    font-weight: bold;
    text-transform: uppercase;
    background: transparent;
    
    vertical-align: top;
    border-radius: 8px;
    cursor: pointer;
  }
  
  .skill-button:hover {
    background: linear-gradient(to top, #000000 0%, #000000ae 10%);
  }
  
  .skill-button:focus {
    
    outline: none;
  }

  .skill {
    margin-bottom: 10px;
  }
  .TabHeading{
    text-align: center;
    margin-bottom: 2rem;
    font-size: 1.5rem;
  }
  .skillhead{
    font-size: 1rem;
  }
  .h2h3{
      text-align: center;
      margin: 4rem;
  }
  .skillHead{
    font-size: 2.2rem;
  }
  .Technical{
    font-size: 1.3rem;
    color: #e2dada;
  }
  .skills {
    display: flex;
    place-content: center;
    flex-wrap: wrap;
    margin: 20px;
    /* justify-content: space-between; */
  }
  .column {
    width: 60%;
    box-sizing: border-box;
    padding: 5px;
  }
  .card-heading {
    background-color: transparent;
    padding: 10px;
    margin-bottom: 10px;
    margin: 2rem;
    border: 0.3px solid rgba(68, 92, 107, 0.988);
    box-shadow: 0 6px 8px rgba(57, 115, 152, 0.988);
    border-radius: 10px;
  }
  .align{
    display: flex;
  }
  .exp{
    font-size: 12px;
  }

  .icon{
    padding: 5px;
  }

  
@media (max-width:1080px) {
  .skills {
    justify-content: center;

  }
}
  